@import url(https://fonts.googleapis.com/css?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:700&display=swap);
body {
  box-sizing: border-box;
}

.App {
  display: grid;
  height: 79vh;
  max-width: 1080px;
  position: relative;

  justify-content: center;
  border-top: 10px solid black;
  background: gray;
  
  overflow: hidden;
  margin: 0 auto;
  font-family: 'Permanent Marker', cursive;
  color: #fff;
  text-transform: uppercase;
}

.App h2 {
  margin: 0 auto;
  object-fit: cover;
  font-size: 12px;
  font-weight: normal;
}

.App-header {
  display: grid;
  grid-column-gap: 5px;
  grid-row-gap: 3px;
  align-self: center;
  text-align: end;
  grid-template-columns: repeat(7, 50px);
}

.App-header form:nth-child(11) {
  grid-column-start: 4;
  grid-column-end: 6;
  padding-left: 55px;
  position: left;
  background: no-repeat;
  background-size: 50px;
  background-image: url(http://icons.iconarchive.com/icons/kalangozilla/skrynium-black/256/mortal-kombat-icon.png);
}
.App-header form:nth-child(14) {
  grid-row-start: 3;
  grid-row-end: 6;
  position: relative;
}
.App-header form:nth-child(20) {
  grid-row-start: 3;
  grid-row-end: 6;
  grid-column-start: 7;
  grid-column-end: 8;
  opacity: 0;
}
.for_decoration {
  height: 140px;
}
.footer {
  background: black;
  height: 20px;
}
.mc__VsScreen_box {
  background: url(http://luxfon.com/large/201203/9589.jpg);
  height: 100%;
  position: absolute;
  top: 0;
  margin: 0 auto;
  width: 100%;
  display: grid;
  grid-template-columns: 150px 1fr 515px 1fr 150px;;
}

.mortal-Combat_logo-right {
  height: 50px;
}
.mortal-Combat_logo-left {
  height: 50px;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.mc__VsScreen__img-left {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.mortal-Combat_logo-vs {
  height: 15px;
  background: none;
}
.mc__VsScreen_wall {
  display: block;
  width: 150px;
  height: inherit;
} 
.mc__VsScreen_wall img {
  display: block;
  object-fit: cover;
  height: 100%;
  max-width: 100%;
}
.mortal-Combat_container {
  display: flex;
}
.mortal-Combat_main {
  display: flex;
  margin: 0 auto;
  flex-direction: column;
  height: 100%;
}
.mortal-Combat_picture {
  display: block;
  width: 133px;
  height: inherit;
  position: relative;
  top: 100px;
}

.mortal-Combat_picture img {
  display: block;
  object-fit: cover;
  height: 250px;
  max-width: 100%;
}
.mortal-Combat_main h2 {
  margin-bottom: 140px;
}

.mortal-Combat_main img {
  width: 230px;
  height: 130px;
}


  .mc__VsScreen_box_animation {
   position: absolute;
   display: flex;
   justify-content: center;
   width: 100%;
   z-index: 5;
  }
.mc__VsScreen_wall_animation {
  width: 600px;
}
.mc__VsScreen_wall_animation_right {
 width: 600px;
 -webkit-animation: animdash 3s;
         animation: animdash 3s; 
 -webkit-transition: ease; 
 transition: ease;
}
  .mc__VsScreen__img-left_animation {
   -webkit-transform: rotate(180deg);
           transform: rotate(180deg);
   width: 100%;
}
.mc__VsScreen__img-right_animation {
  width: 100%;
}
 
@-webkit-keyframes animdash {
 0% {
  margin-right: 0;
 }
 50% {
  margin-right: 300px;
 }
 100% {
  margin-right: 640px;
 }
}
 
@keyframes animdash {
 0% {
  margin-right: 0;
 }
 50% {
  margin-right: 300px;
 }
 100% {
  margin-right: 640px;
 }
}
@-webkit-keyframes animdashg {
 0% {
  margin-left: 0;
 }
 50% {
  margin-left: 300px;
 }
 100% {
  margin-left: 640px;
 }
}
@keyframes animdashg {
 0% {
  margin-left: 0;
 }
 50% {
  margin-left: 300px;
 }
 100% {
  margin-left: 640px;
 }
}


.PeopleTable__row--selected {
  border: 4px solid rgb(61, 226, 28) !important;;
  position: relative;
  top: -4px;
  left: -4px;
  box-sizing: inherit;
}
.PeopleTable__row--selected::after {
  content: "1";
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: rotate(18deg);
          transform: rotate(18deg);
  color: rgb(244, 246, 216);
  font-family: 'Roboto', sans-serif;
}

.person__img {
  display: block;
  position: absolute;
  top: 0;
  height: 100px;
  width: 50px;
  background: gray;
}
.person__img img {
  height: 100%;
  width: 100%;
  object-fit: initial;
}

.person__img__hidden {
 display: none;
}

.form {
  height: 50px;
  border: 1px solid gray;
}
.person {
  width: 50px;
  height: 100%;
  padding: 0;
  border: none;
}
button {
  cursor: pointer;
}
.person__img_icon {
  display: block;
  object-fit: cover;
  height: 100%;
  max-width: 100%;
  margin-bottom: 0;
  -webkit-transform: scale(1);
  transform: scale(1);
}

